@import url(https://fonts.googleapis.com/css?family=Roboto);
*{padding:0;margin:0;border:0;box-sizing:border-box}*:focus{outline:0}body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:#fff}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}button{cursor:pointer}h1{margin-bottom:15px}h2,h3,h4,h5,h6{margin-bottom:10px}.btn{padding:15px;color:white;border-radius:5px;font-size:24px;display:block;text-decoration:none;cursor:pointer;text-align:center}.btn.btn-primary{background-color:purple;box-shadow:0 5px 0 #640064}.btn.btn-secondary{background-color:#3588c4;box-shadow:0 5px 0 #286591}

.App{width:100%;font-family:'Roboto', sans-serif;color:purple;background:white}.App header{padding:15px}.App header h1{margin:0}.App .App-container{width:100%;margin-top:100px;display:flex;justify-content:center}@media (max-width: 900px){.App .App-container{margin-top:50px}}

.Anime .Anime-container{margin-bottom:20px}.Anime .Anime-container .Anime-fields{display:flex;margin-bottom:20px}.Anime .Anime-container .Anime-fields .Anime-img{max-width:300px}.Anime .Anime-container .Anime-fields .Anime-img img{width:100%}@media (max-width: 900px){.Anime .Anime-container .Anime-fields .Anime-img{margin-bottom:30px}}.Anime .Anime-container .Anime-fields .Anime-information{margin-left:20px;max-width:650px}.Anime .Anime-container .Anime-fields .Anime-information ul.stats{list-style:none}.Anime .Anime-container .Anime-fields .Anime-information ul.stats li{padding:5px 0}@media (max-width: 650px){.Anime .Anime-container .Anime-fields .Anime-information{margin-left:10px;margin-right:10px}}.Anime .Anime-container .Anime-fields .synopsis-section{padding:10px 0}@media (max-width: 900px){.Anime .Anime-container .Anime-fields{flex-direction:column;align-items:center}}.Anime .Anime-container .buttons-container{display:flex;justify-content:center;padding:10px 0}.Anime .Anime-container .buttons-container .btn{margin-left:10px}.Anime .Anime-container .buttons-container .btn:first-child{margin-left:0}@media (max-width: 600px){.Anime .Anime-container .buttons-container{flex-direction:column;margin:0 10px 10px 10px}.Anime .Anime-container .buttons-container .btn{margin-left:0 !important;margin-bottom:15px}}

.Loading{max-width:500px}.Loading img{width:100%}

